@font-face {
  font-family: "Orbitron";
  src: url("/assets/fonts/Orbitron/static/Orbitron-Regular.ttf") format("ttf");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Chakra Petch";
  src: url("/assets/fonts/Chakra_Petch/ChakraPetch-Regular.ttf") format("ttf");
  font-weight: normal;
  font-style: normal;
}
html {
  scroll-behavior: smooth;
}

@media (prefers-color-scheme: dark) {
  body.light-mode .theme-switcher .theme-toggler {
    box-shadow: 2px 2px 5px var(--text-color);
  }
}
@media (prefers-color-scheme: light) {
  body:not(.light-mode) .theme-switcher .theme-toggler {
    box-shadow: 2px 2px 5px var(--text-color);
  }
}
body {
  position: relative;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Apple Color Emoji", "Segoe UI Emoji", "Noto Color Emoji";
}
body .orbitron-font {
  font-family: "Orbitron", monospace;
}
body .chakra-font {
  font-family: "Chakra Petch", monospace;
}
body .white-font {
  --bg-color: #000;
  --text-color: #FFF;
  --shadow-color: rgba(255, 255, 255, 0.05);
  background-color: #000;
  color: #FFF;
}
body h1 {
  font-family: "Orbitron", monospace;
}
body h2, body h3, body h4 {
  font-family: "Chakra Petch", monospace;
}
body a, body button, body input {
  font-family: "Chakra Petch", monospace;
  display: inline-block;
  touch-action: manipulation;
  background-color: transparent;
  text-decoration: none;
  cursor: pointer;
}
body a:hover, body button:hover, body input:hover {
  opacity: 0.6;
  transition: opacity 0.1s ease;
}
body, body a, body button, body input {
  --bg-color: #1E1E1E;
  --text-color: #D4D4D4;
  --shadow-color: rgba(255, 255, 255, 0.05);
  background-color: #1E1E1E;
  color: #D4D4D4;
}
body .black-white-wrapper, body .black-white-wrapper a, body .black-white-wrapper button, body .black-white-wrapper input {
  --bg-color: #000;
  --text-color: #FFF;
  --shadow-color: rgba(255, 255, 255, 0.05);
  background-color: #000;
  color: #FFF;
}
body.light-mode, body.light-mode a, body.light-mode button, body.light-mode input {
  --bg-color: #FAFAFA;
  --text-color: #333333;
  --shadow-color: rgba(0, 0, 0, 0.05);
  background-color: #FAFAFA;
  color: #333333;
}
body.light-mode .black-white-wrapper, body.light-mode .black-white-wrapper a, body.light-mode .black-white-wrapper button, body.light-mode .black-white-wrapper input {
  --bg-color: #FFF;
  --text-color: #000;
  --shadow-color: rgba(0, 0, 0, 0.05);
  background-color: #FFF;
  color: #000;
}
body.transitioning-mode, body.transitioning-mode * {
  transition: all 0.3s ease;
}

.sr-only,
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

.no-wrap {
  white-space: nowrap;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}
@media (max-width: 1200px) {
  .container {
    padding: 0 15px;
  }
}

header .header-sticky {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1;
  box-shadow: 0 2px 8px var(--text-color);
}
header .header-wrapper {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
header .header-wrapper .menu-wrapper {
  width: 100%;
  display: flex;
  justify-content: flex-end;
}

.logo-container {
  padding: 20px 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.logo-container .logo {
  font-size: 20px;
  font-weight: bold;
}

.menu-toggle {
  display: none;
}

.nav-wrapper {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

nav {
  width: 100%;
  display: flex;
  justify-content: center;
}
nav ul {
  display: flex;
  flex-direction: row;
  gap: 30px;
  justify-content: center;
  padding-left: 0;
  list-style-type: none;
}
nav a {
  position: relative;
  font-weight: bold;
}
nav a::after {
  content: "";
  position: absolute;
  width: 0%;
  height: 2px;
  bottom: -2px;
  left: 0;
  background-color: var(--text-color);
  transition: width 0.3s ease;
}
nav a:hover::after {
  width: 100%;
}

.theme-switcher {
  display: flex;
  justify-content: center;
  align-items: center;
}
.theme-switcher .theme-toggler {
  position: relative;
  padding: 4px 5px;
  border: 1px solid var(--text-color);
  border-radius: 15px;
}
.theme-switcher .theme-toggler span {
  font-size: 16px;
  line-height: 20px;
}
.theme-switcher .theme-toggler .marker {
  position: absolute;
  top: 3px;
  left: 4px;
  width: 22px;
  height: 22px;
  border-radius: 22px;
  color: transparent;
  background-color: var(--text-color);
  transition: all ease-in 0.3s;
}
.theme-switcher .theme-toggler.light-mode .marker {
  left: calc(100% - 26px);
}

section {
  text-align: center;
  box-shadow: 0 6px 8px var(--shadow-color);
  padding: 60px 20px;
}
@media (min-width: 768px) {
  section {
    padding: 80px 20px;
  }
}
@media (min-width: 1200px) {
  section {
    padding: 100px 20px;
  }
}

#about p {
  max-width: 820px;
  margin: 0 auto 20px;
  line-height: 1.5rem;
}

#banner {
  padding: 65px 0 0;
  background: url("https://img.freepik.com/premium-photo/close-up-hands-typing-keyboard-with-code-computer-screen_1187703-176393.jpg") center/cover no-repeat;
  box-shadow: inset 0 0 100px rgba(0, 0, 0, 0.4);
  min-height: 60vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#banner h1 {
  font-weight: 700;
}

#offer .circle-container {
  display: flex;
  justify-content: center;
  gap: 2rem;
  flex-wrap: wrap;
}

.circle {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  color: var(--bg-color);
  background-color: var(--text-color);
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  padding: 1rem;
}
.circle .card-back {
  display: none;
}

#offer .offer-grid-wrapper {
  max-width: 820px;
  margin: 0 auto;
}

@media (min-width: 1200px) {
  .offer-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}
@media (max-width: 1199px) {
  .offer-grid.mobile-reverse {
    display: flex;
    flex-direction: column-reverse;
  }
}
.offer-grid > div {
  display: flex;
  justify-content: center;
  align-items: center;
}
.offer-grid ul {
  padding-left: unset;
  list-style-type: none;
}
.offer-grid ul li {
  margin-bottom: 5px;
}

#map > div {
  margin: 0 auto;
  max-width: 900px;
}
#map iframe {
  width: 100%;
  height: 400px;
  border: none;
}

#footer {
  font-size: 0.9rem;
  padding: 60px 0 100px;
  text-align: center;
}

@media (max-width: 768px) {
  .menu-toggle {
    display: block;
  }
  .nav-wrapper {
    display: flex;
    flex-direction: column;
    padding: 10px 0 20px;
    background-color: var(--bg-color);
    box-shadow: -2px 2px 4px var(--text-color);
    overflow: hidden;
    position: absolute;
    top: 69px;
    right: -100%;
    transition: right 0.3s ease;
  }
  .nav-wrapper.show {
    right: 0;
  }
  .nav-wrapper nav ul {
    display: flex;
    flex-direction: column;
    row-gap: 0;
    padding-left: 0;
  }
  .nav-wrapper nav li {
    padding: 10px 0;
    text-align: center;
  }
}
.hamburger {
  position: relative;
  width: 30px;
  height: 24px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
}
.hamburger span {
  display: block;
  height: 4px;
  width: 100%;
  background-color: var(--text-color);
  border-radius: 2px;
  transition: transform 0.4s ease, opacity 0.4s ease;
}
.hamburger.active span:nth-child(1) {
  transform: translateY(10px) rotate(45deg);
}
.hamburger.active span:nth-child(2) {
  opacity: 0;
}
.hamburger.active span:nth-child(3) {
  transform: translateY(-10px) rotate(-45deg);
}

.cta-contact {
  background-color: #F1DD0A !important;
  padding: 10px 20px;
  border-radius: 30px;
  border: 2px solid var(--text-color);
  color: #000;
  font-weight: 500;
}

#copy-to-clipboard-button {
  display: inline-block;
  margin: 0 auto;
  padding-right: 25px;
  padding-bottom: 12px;
  position: relative;
  cursor: pointer;
}
#copy-to-clipboard-button:hover {
  opacity: 0.5;
}
#copy-to-clipboard-button::before {
  content: "";
  display: inline-block;
  width: 12px;
  height: 12px;
  background-color: #c7c7c7;
  border-radius: 2px;
  position: absolute;
  top: 4px;
  right: 4px;
}
#copy-to-clipboard-button::after {
  content: "";
  display: inline-block;
  width: 12px;
  height: 12px;
  background-color: #5f5f5f;
  border-radius: 2px;
  position: absolute;
  top: 8px;
  right: 0;
}
#copy-to-clipboard-button {
  transition: all ease-in 0.9s;
}
#copy-to-clipboard-button.clicked::before {
  top: 3px;
  right: 5px;
}
#copy-to-clipboard-button.clicked::after {
  top: 9px;
  right: -1px;
}